<template>
  <div>
    <a-row type="flex" justify="center">
      <a-col :span="21" class="nta-web-label">FONT FAMILY</a-col>
    </a-row>
    <a-row type="flex" justify="center">
      <a-col :span="21">
        <a-select
          class="nta-web-select"
          dropdownClassName="ntaWebDropdown"
          :value="contentSetRow.settingRow.family"
          @change="changeFamily"
        >
          <a-select-option v-for="(family, key) in familys" :key="key" :value="family">
            <span :style="{ fontFamily: family }">{{ family }}</span>
          </a-select-option>
        </a-select>
      </a-col>
    </a-row>
  </div>
</template>
<script>

export default {
  created(){
    window.yaymailCreatedCoreVue.done((coreVue) => {
      this.contentSetRow = coreVue.$store.state.settingRow.contentSetRow;
    });
  },
  data() {
    return {
      familys: [
        '"Helvetica Neue",Helvetica,Roboto,Arial,sans-serif',
        'Georgia, serif',
        '"Times New Roman", Times, Serif',
        'Arial, Helvetica, sans-serif',
        '"Arial Black", Gadget, sans-serif',
        '"Comic Sans MS", cursive, sans-serif',
        'Tahoma, Geneva, sans-serif',
        '"Trebuchet MS", Helvetica, sans-serif',
        'Verdana, Geneva, sans-serif',
        '"Courier New", Courier, monospace',
        '"Lucida Console", Monaco, monospace'
      ],
      contentSetRow: null,
    };
  },
  methods: {
    changeFamily(family) {
      this.$store.dispatch("settingRow/changeSettingRow", {valueAttribute: family, attributeSet: 'family'});
    }
  }
};
</script>

<style scoped>
.nta-web-select {
  width: 100%;
  padding: 8px 0px;
}

.nta-web-label {
  font-size: 12px;
  font-weight: 500;
  color: #b9b9b9;
  margin-top: 24px;
}
</style>