<template>
  <div>
    <a-row type="flex" justify="center" class="nta-web-setting-label">
      <a-col :span="21">TEXT COLOR</a-col>
    </a-row>
    <a-row type="flex" justify="center">
      <a-col :span="21">
        <div ref="colorpickertext">
          <div class="nta-web-current-color" @click="togglePicker()">
            <div
              class="nta-web-color"
              :style="{ backgroundColor: contentSetRow.settingRow.textColor }"
            ></div>
            <p class="nta-web-item-color-title">Select Color</p>
          </div>
          <sketch-picker
            @input="changeFromPicker"
            class="nta-web-sketch-picker nta-web-sketch-picker-title"
            :value="contentSetRow.settingRow.textColor"
            v-if="displayPicker"
          />
        </div>
      </a-col>
    </a-row>
  </div>
</template>

<script>
import { Sketch } from "vue-color";

export default {
  components: {
    "sketch-picker": Sketch
  },
  data() {
    return {
      displayPicker: false,
      contentSetRow: null,
    };
  },
  created(){
    window.yaymailCreatedCoreVue.done((coreVue) => {
      this.contentSetRow = coreVue.$store.state.settingRow.contentSetRow;
    });
  },
  methods: {
    showPicker () {
      document.addEventListener('click', this.documentClick);
      this.displayPicker = true;
    },
    hidePicker () {
        document.removeEventListener('click', this.documentClick);
        this.displayPicker = false;
    },
    togglePicker() {
      this.displayPicker ? this.hidePicker() : this.showPicker();
    },
    documentClick (e) {
      const el = this.$refs.colorpickertext;
      const target = e.target;
      if (el === undefined || el !== target && !el.contains(target)) {
          this.hidePicker();
      }
    },
    changeFromPicker(color) {
      if (color.rgba.a == 1) {
        this.$store.dispatch("settingRow/changeSettingRow", {
          valueAttribute: color.hex,
          attributeSet: "textColor"
        });
      } else {
        const colorValue =
          "rgba(" +
          color.rgba.r +
          ", " +
          color.rgba.g +
          ", " +
          color.rgba.b +
          ", " +
          color.rgba.a +
          ")";
        this.$store.dispatch("settingRow/changeSettingRow", {
          valueAttribute: colorValue,
          attributeSet: "textColor"
        });
      }
    }
  }
};
</script>

<style scoped>
.nta-web-setting-label {
  margin-top: 26px;
  font-size: 12px;
  font-weight: 500;
  color: #b9b9b9;
}

.nta-web-content-background-color {
  width: 110px;
  text-align: center;
  display: block;
  background: #fff;
  margin-top: 8px;
  margin-bottom: 8px;
  cursor: context-menu;
}
</style>
