<template>
  <div class="nta-web-padding">
    <a-row type="flex" justify="center" class="nta-web-label">
      <a-col :span="21">
        PADDING:
      </a-col>
    </a-row>
    
    <div class="nta-web-text-wrap">
      <div class="nta-web-text nta-web-text-top">Top</div>
      <div class="nta-web-text nta-web-text-left">Left</div>
      <div class="nta-web-text nta-web-text-right">Right</div>
      <div class="nta-web-text nta-web-text-bottom">Bottom</div>
    </div>

    <a-row type="flex" justify="center" class="nta-web-setting-content">
      <a-col :span="21">
        <a-input-number
          class="nta-web-setting-padding"
          :min="0"
          :value="Number(contentSetRow.settingRow.paddingTop)"
          @change="changePaddingTop"
        />
        <a-input-number
          class="nta-web-setting-padding"
          :min="0"
          :value="Number(contentSetRow.settingRow.paddingLeft)"
          @change="changePaddingLeft"
        />
        <a-input-number
          class="nta-web-setting-padding"
          :min="0"
          :value="Number(contentSetRow.settingRow.paddingRight)"
          @change="changePaddingRight"
        />
        <a-input-number
          class="nta-web-setting-padding"
          :min="0"
          :value="Number(contentSetRow.settingRow.paddingBottom)"
          @change="changePaddingBottom"
        />
      </a-col>
    </a-row>
  </div>
</template>

<script>

export default {
  data(){
    return {
      contentSetRow: null,
    }
  },  
  created(){
    window.yaymailCreatedCoreVue.done((coreVue) => {
      this.contentSetRow = coreVue.$store.state.settingRow.contentSetRow;
    });
  },
  methods: {
    changePaddingTop(value) {
      this.$store.dispatch("settingRow/changeSettingRow", {valueAttribute: value, attributeSet: 'paddingTop'});
    },
    changePaddingRight(value) {
      this.$store.dispatch("settingRow/changeSettingRow", {valueAttribute: value, attributeSet: 'paddingRight'});
    },
    changePaddingBottom(value) {
      this.$store.dispatch("settingRow/changeSettingRow", {valueAttribute: value, attributeSet: 'paddingBottom'});
    },
    changePaddingLeft(value) {
      this.$store.dispatch("settingRow/changeSettingRow", {valueAttribute: value, attributeSet: 'paddingLeft'});
    }
  }
};
</script>

